* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

a {
  text-decoration: none;
}

.header {
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

input {
  height: 50px;
  width: 358px;
  box-sizing: border-box;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(37, 37, 37, .3);
  border-radius: 4px;
  padding: 20px;
  font-weight: 700;
}

.search-btn {
  width: 100px;
  height: 50px;
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  background: #1ed31e;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more {
  height: 50px;
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  background: #1ed31e;
  border: none;
  border-radius: 4px;
  align-items: center;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more.is-hidden, .load-more > .spinner.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.photo-card {
  width: 320px;
  margin-bottom: 20px;
}

.photo-card:hover {
  color: #fff;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.info {
  text-align: center;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #eee;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info-item {
  color: #000;
  margin-top: 3px;
  font-family: Kdam Thmor Pro, sans-serif;
}

.result {
  color: #757575;
  font-family: Kdam Thmor Pro, sans-serif;
  font-size: 14px;
  line-height: 1.88;
}

